  border: 2px solid #fd645b;
  -webkit-transition: .01s;
  -o-transition: .01s;
  transition: .01s;
}

.form-radio .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.form-radio .radio input:checked~.checkmark:after {
  display: block;
  border: 1px solid #fd645b;
}

