.ftco-counter {
  overflow-x: hidden;
  position: relative;
  padding: 5em 0;
  background: #4c7bcb;
}
  @media (max-width: 1199.98px) {
    .ftco-counter {
      background-position: center center !important; } }
  .ftco-counter .block-18 {
    display: block;
    width: 100%; }
    @media (max-width: 991.98px) {
      .ftco-counter .block-18 {
        margin-bottom: 2em; } }
  .ftco-counter .text {
    position: relative; }
    @media (max-width: 767.98px) {
      .ftco-counter .text {
        width: 100%; } }
    .ftco-counter .text .number {
      line-height: 1;
      font-weight: 800;
      font-size: 40px;
      color: #fd645b;
      display: block;
      margin-bottom: 10px; }
    .ftco-counter .text span {
      font-size: 14px;
      color: rgba(255, 255, 255, 0.8);
      font-weight: 600; }
  @media (max-width: 767.98px) {
    .ftco-counter .counter-wrap {
      margin-bottom: 20px; } }
